/* eslint-env browser */
import React from 'react';
import { render, unmountComponentAtNode } from 'react-dom';

import { NO_LAYOUT_CLASS, CONTAINER_CLASS } from '../../../../lib/specs/render';
import { parse } from '../../../../lib/specs/serialize';

let chromaticRoot;

let drawn = false;
function drawIsolator() {
  if (drawn) {
    return;
  }
  const styles = `
  body {
    margin: 0;
  }
  /* ensure a given element is not laid out at all */
  .${NO_LAYOUT_CLASS} {
    margin: 0 !important;
    padding: 0 !important;
    position: static !important;
  }
  `;
  const el = document.createElement('style');
  el.type = 'text/css';
  el.innerHTML = styles;
  document.head.appendChild(el);

  chromaticRoot = document.createElement('div');

  // Hide all existing content
  document.querySelectorAll('body > *').forEach(e => {
    e.style.display = 'none';
  });

  // Add our root
  document.body.appendChild(chromaticRoot);
  drawn = true;
}

export default function configure({ runtimes, clearScreen }) {
  if (clearScreen) {
    drawIsolator();
  }

  const runtimeRenderers = {};
  runtimes.forEach(r => {
    runtimeRenderers[r.runtime] = r.renderSpec;
  });

  function runtimeSpecs() {
    return runtimes
      .map(({ runtime, specs }) => specs().map(spec => ({ runtime, ...spec })))
      .reduce((a, b) => [...a, ...b], []);
  }

  function renderSpecToDom(spec) {
    drawIsolator();

    const parsedSpec = {
      ...spec,
      input: typeof spec.input === 'string' ? parse(spec.input) : spec.input,
    };

    let element;
    if (spec.runtime) {
      const renderer = runtimeRenderers[spec.runtime];
      if (!renderer) {
        throw new Error(
          `Didn't find a renderer for runtime '${spec.runtime}' -- are you sure you added it in your client configuration?`
        );
      }
      const renderedSpec = renderer(parsedSpec);
      element = <div className={CONTAINER_CLASS}>{renderedSpec}</div>;
    } else {
      throw new Error('No runtime passed for spec', spec);
    }

    unmountComponentAtNode(chromaticRoot);
    render(element, chromaticRoot);
  }

  // API to be used by test script to gather runtime spec definitions
  window.__chromaticRuntimeSpecs__ = runtimeSpecs;

  // API to be used by capture worker to render a spec
  window.__renderChromaticSpec__ = renderSpecToDom;

  function handleMessage({ data, source, origin }) {
    // TODO -- is it unsafe to not check origin? I don't get the sense it matters

    const { message, ...rest } = data;
    if (message === 'requestCapabilities') {
      source.postMessage(
        {
          message: 'capabilities',
          service: 'chromatic-isolator',
          version: 0,
        },
        origin
      );
    } else if (message === 'renderSpec') {
      try {
        renderSpecToDom(rest.spec);
      } catch (error) {
        source.postMessage(
          {
            message: 'renderSpecError',
            error: error.toString(),
          },
          origin
        );

        // For now, throw the error so we can see it in the iframe
        throw error;
      }
    }
  }

  window.addEventListener('message', handleMessage, false);
}



