// Render a spec in our format to a React element.
// Note that this is *NOT* a stateless functional component
// We *don't* want to end up with our own element type, instead
// we need to construct an element tree *as we saved it*.
/* eslint-disable react/prop-types */

import React, { createElement } from 'react';

export const NO_LAYOUT_CLASS = '__chromatic__no_layout';
export const CONTAINER_CLASS = '__chromatic_rendered_spec_container';

// We need at least a single div so we can set the CONTAINER_CLASS
const defaultAncestors = [{ name: 'div' }];
// Don't specify if we don't know
const defaultDimensions = { width: '', height: '' };

function wrapInAncestors(element, ancestors, { width, height }) {
  return ancestors.reduceRight((output, { name: Tag, id, className }, index) => {
    const classes = [NO_LAYOUT_CLASS, className];
    if (index === ancestors.length - 1) {
      classes.push(CONTAINER_CLASS);
    }
    return (
      <Tag id={id} className={classes.join(' ')} style={{ width, height }}>
        {output}
      </Tag>
    );
  }, element);
}

function renderInputElement(element, options) {
  if (typeof element !== 'object') {
    return element;
  }

  const { name, props } = element;

  const mappedProps = {};
  let mappedChildren = [];
  if (props) {
    const { children, ...otherProps } = props;

    Object.keys(otherProps).forEach(propName => {
      mappedProps[propName] = options.mapProps(propName, props[propName]);
    });

    if (children) {
      mappedChildren = [].concat(children).map(e => renderInputElement(e, options));
    }
  }

  const component = options.nameToComponent(name);

  return createElement(component, mappedProps, ...mappedChildren);
}

export default function renderSpec(
  { input: { element, ancestors = defaultAncestors, dimensions = defaultDimensions } },
  options = { nameToComponent: name => name, mapProps: (name, value) => value }
) {
  const reactElement = renderInputElement(element, options);

  return wrapInAncestors(reactElement, ancestors, dimensions);
}



